body {
  margin: 0;
}

#nav {
  background: -webkit-repeating-linear-gradient(45deg, #2b2b2b 0%, #2b2b2b 10%, black 0%, black 50%) 0 / 15px 15px;
  background: -o-repeating-linear-gradient(45deg, #2b2b2b 0%, #2b2b2b 10%, black 0%, black 50%) 0 / 15px 15px;
  background: repeating-linear-gradient(45deg, #2b2b2b 0%, #2b2b2b 10%, black 0%, black 50%) 0 / 15px 15px;
  margin: 0;
}

span {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

#backSection {
  float: left;
  width: 33.33333%;
  text-align: left;
  margin-top: 10px;
}

#back {
  text-decoration: none;
  color: black;
  font-size: 1em;
  font-family: 'Futura';
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;
  margin-left: 20px;
  margin-top: 1em;
  font-size: 2.25em;
}

#back:hover {
  text-decoration: none;
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  font-size: 2.5em;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ffff00, 0 0 30px #ffff00, 0 0 40px #ffff00, 0 0 55px #ffff00, 0 0 75px #ffff00;
}

#logo {
  float: left;
  width: 33.33333%;
  text-align: center;
  height: 10em;
  width: auto;
  margin-top: 30px;
}

#right {
  float: left;
  width: 33.33333%;
  text-align: right;
}

#title {
  font-size: 3em;
  font-family: 'Futura';
  color: #fff;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;
  text-align: center;
  width: 100%;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#root {
  vertical-align: center;
}

.App-footer {
  font-size: 1em;
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: center;
  font-family: 'Futura';
  height: 135px;
  background-color: black;
  background: -webkit-repeating-linear-gradient(45deg, #2b2b2b 0%, #2b2b2b 10%, black 0%, black 50%) 0 / 15px 15px;
  background: -o-repeating-linear-gradient(45deg, #2b2b2b 0%, #2b2b2b 10%, black 0%, black 50%) 0 / 15px 15px;
  background: repeating-linear-gradient(45deg, #2b2b2b 0%, #2b2b2b 10%, black 0%, black 50%) 0 / 15px 15px;
  color: black;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ffff00, 0 0 30px #ffff00, 0 0 40px #ffff00, 0 0 55px #ffff00, 0 0 75px #ffff00;
  text-align: center;
  margin-bottom: 0;
}

.App-heart {
  position: center;
  height: 70px;
  text-align: center;
  padding-top: 10px;
}



